html,body{
	margin:0px;
	padding:0px;
}

body{
	width:100%;
	height:100%;
	overflow:hidden;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	//background-color:#ccc;

	&.scrolled{
		.header{
			height:60px;
			background-color:$white;
			border-bottom-color:$gray;

			nav ul li a{
				color:$dark-gray;

				&:hover{
					color:$dark-blue;
				}
			}

			nav .nav__links a{
				color:$dark-gray;

				&:hover{
					color:$dark-blue;
					&:after{
						background-color:$dark-blue;
					}
				}
			}

			nav .nav__icons__media{
				border-left-color:$gray;

			}

			nav .nav__icons__media a, nav .nav__icons__search a{
				font-size:16px;
				margin-right:20px;
			}

			.logo__white{
				opacity:0;
			}

			.logo__blue{
				opacity:1;
			}
		}
	}
}

.page_container{
	position:absolute;
	box-sizing:border-box;
	left:0px;
	top:0px;
	width:100%;
	height:100%;
	overflow-y:auto;
}

.container{
	margin:auto;
	position:relative;
	display:block;
	width:95%;
	max-width:1170px;
	box-sizing:border-box;
}

